@keyframes messageShake {
  0% {
    transform: translate3d(0, 0px, 0) rotate(0deg); }
  10% {
    transform: translate3d(0, -10px, 0) rotate(5deg); }
  20% {
    transform: translate3d(0, -20px, 0) rotate(0deg); }
  30% {
    transform: translate3d(0, -10px, 0) rotate(-5deg); }
  40% {
    transform: translate3d(0, 0px, 0) rotate(0deg); }
  50% {
    transform: translate3d(0, -10px, 0) rotate(5deg); }
  60% {
    transform: translate3d(0, -20px, 0) rotate(0deg); }
  70% {
    transform: translate3d(0, -10px, 0) rotate(-5deg); }
  80% {
    transform: translate3d(0, 0px, 0) rotate(0deg); }
  90% {
    transform: translate3d(0, -10px, 0) rotate(5deg); }
  100% {
    transform: translate3d(0, 0px, 0) rotate(0deg); } }

